.Category {
	font-size: 1.25rem;
}

.Category form {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 10px 0;
	text-shadow: -1px 1px 1px black;
}

.Category img {
	transition: transform 0.3s;
	border: 1px solid #084949;
	border-radius: 10px;
	box-shadow: -4px -2px 10px black;
}

li {
	text-shadow: -1px 1px 1px black;
}
